import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

const initialState = {};

function getWorkoutColor(workout) {
	switch (workout.type) {
		case 1:
			return 'border-primary';	
		case 2:
			return 'border-danger';
		case 3:
			return 'border-success';
		default:
			break;
	}
}

function getWorkoutIcon(workout) {
	switch (workout.type) {
		case 1:
			return 'fas fa-running';
		case 2:
			return 'fas fa-flag-checkered';
		case 3:
			return 'fas fa-hiking';
		default:
			break;
	}
}

@observer
class Workouts extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadWorkouts();
    }

	loadWorkouts() {
		const { workoutStore } = this.props.stores;
		workoutStore.load();
	}

	render() {
		const { workoutStore, userStore } = this.props.stores;
		const { workouts } = workoutStore;
		const { user } = userStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12'>
						<a class='btn btn-primary btn-sm float-right'
							href='/workouts/edit/new'
						>
							<i class='fas fa-plus' /> Legg til treningstur
						</a>
					</div>
					<div class='col-12'>
						<h5>Treningsturene - {workouts.length} stk</h5>

						<div class='row'>
							{workouts && workouts.map(workout => (
								<div class='col-12 col-sm-6 col-lg-2 mt-2 p-2 d-flex'>
									{/* {JSON.stringify(workout, null, 4)} */}
									<div class={`text-center border rounded-circle imageRounded ${getWorkoutColor(workout)}`} style={workout.image ? `background-image: url("${workout.image.s3SmallLink}");` : ''}>
										{!workout.image && <i class={`${getWorkoutIcon(workout)} text-muted mt-3`} style='font-size: 40px;' />}
									</div>
									<div class='flex-grow-1 pl-3'>
										<a class='stretched-link' href={`/workouts/${workout.id}`}>
											<h5 class='my-0'>{workout.name}</h5>
										</a>
										{userStore.findTeam(workout.team)}<br />
										<small>
											<span class='text-muted'>
												<i class='fas fa-tools' /> {workout.equipment}
												<i class='fas fa-road ml-2' /> {workout.distanceKm}km
												<i class='fas fa-mountain ml-2' /> {workout.elevation}m
												{workout.dogs && <span><i class='fas fa-dog ml-2' /> {workout.dogs.length}</span>}
											</span>
											<i>{workout.url}</i><br />
										</small>
										<small class='text-muted'>
											{util.isoDate(workout.date, false, false, true)}
										</small>
									</div>
								</div>
							))}
						</div>

					</div>
					<div class='col-12'>
						<a class='btn btn-primary btn-sm mt-5 float-right'
							href='/workouts/edit/new'
						>
							<i class='fas fa-plus' /> Legg til treningstur
						</a>
					</div>
				</div>
			</div>
		);
	}
}

export default Workouts;
